body {
  background: #f3f3f3;
  font-size: 18px;
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.title {
  text-align: center;
  font-size: 2.4rem;
  letter-spacing: 0.08ch;
}

.input-search {
  margin: 0 20px 10px;
}

.input-search input {
  width: 100%;
  font-size: 1.35rem;
  border: 1px solid #a9a9a9;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 10px 8px;
}

.cardlist {
  border-top: 1px solid #a9a9a9;
  margin-top: 20px;
}

.card {
  padding: 14px;
  border-bottom: 1px solid #a9a9a9;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
}

.card:hover {
  background: #d9d9d9;
}

.card:hover .copy-emoji {
  display: inline;
}

.emoji {
  font-size: 1.4rem;
}

.emoji-title {
  margin-left: 10px;
  vertical-align: middle;
}

.copy-emoji {
  position: absolute;
  right: 20px;
  cursor: pointer;
  background: #c6c6c6;
  font-size: 0.9rem;
  padding: 4px 6px;
  border: none;
  border-radius: 5px;
  transition: all 0.3s;
  display: none;
}

.copy-emoji:hover {
  background: #28a745;
  color: white;
}

.copy-emoji:focus {
  outline: none;
}